XDialog
},
created(){
this.getList(1,16);
let heightS = document.documentElement.clientHeight-90+"px";
this.heightS=heightS;
},
methods:{

openDialog(a,b,c,d,e,f,g,h,i,j,k,l,m,n,o,p){













this.order_snS = a
this.createds_atS = b
this.user_nameS = c
this.user_phoneS = d
this.projectsS = e
this.operation_timeS = f
this.moneyS = g
this.hospital_nameS = h
this.service_nameS = i
this.service_phoneS = j
this.areS = k
this.areaS = l
this.all_priceS = m
this.consume_moneyS = n
this.surplusS = o
this.apply = p
for(var i=0;i<this.apply.length;i++){
this.moneyS = this.apply[i].money
this.operation_timeS = this.apply[i].operation_time

console.log(this.projectsS)
if(this.projectsS==""){
this.p1=false
}
if(this.moneyS==""){
