},

closeDialog(){
this.showToast = false
},

getList(page,limit){
let data={
"server_id":"165",
"created_at":[localStorage.getItem("waterStartTime"),localStorage.getItem("waterEndTime")],
"hospital_name":localStorage.getItem("wHospVal"),

"service_keyword":localStorage.getItem("wServerVal"),
"user_keyword":localStorage.getItem("wUserVal"),
"page":page,
"limit":limit,
"sortKey":"all_price",
"sort":"desc"

}
axios.post('/v1/flow_sheet',this.$qs.stringify(data))
.then((response)=>{
console.log("-----")
console.log(response)
let resData = response.data.data.data;
this.resData = resData;
this.LoadingSH = false;
if(resData.length==0){
this.pSH = true;
}
})
.catch((error)=>{
this.pSH = true;
this.LoadingSH = false;
console.log(error.response.data.message)

})
},



























































































clientFn(s){
let data={
"server_id":"165",
"createds_at":[localStorage.getItem("subStartTime"),localStorage.getItem("subEndTime")],
"hospital_name":localStorage.getItem("sHospVal"),
"service_name":localStorage.getItem("sServerVal"),
"user_phone":localStorage.getItem("sUserVal"),
"page":this.counter,
"limit":15,
"sortKey":"money",
"sort":s

}
axios.post('/v1/allowance_sheet',this.$qs.stringify(data))
.then((response)=>{
let resData = response.data.allowances.data;
console.log(response)
if(resData.length==0){

}else{
this.resData = resData;
this.LoadingSH = false;
}
})
.catch((error)=>{
this.LoadingSH = false;
console.log(error.response.data.message)
})
},
clientSort(){
this.LoadingSH = true
console.log(123)
if(this.flag==0){
this.clientFn("desc")
this.downdata=[]
this.flag=1;
this.isA=true;
this.isB=false;
this.LoadingSH = false
}else{
this.clientFn("asc")
this.downdata=[]
this.flag=0
this.isA=false;
this.isB=true;
this.LoadingSH = false
}
}
}
}



